// artemis-admin/src/services/routeAnalysisService.ts
import apiService from './apiService';

// Tip tanmlar
export interface RoutePoint {
  latitude: number;
  longitude: number;
  speed: number;
  date: string;
  mileage: number;
  address: string;
  ignition: boolean;
}

export interface MileageReport {
  totalDistance: number;
  dailyBreakdown: {
    date: string;
    distance: number;
  }[];
}

export interface WorkingHoursReport {
  totalWorkingHours: number;
  totalIdleHours: number;
  dailyBreakdown: any[];
}

export interface HeatPoint {
  latitude: number;
  longitude: number;
  intensity: number;
  duration: number;
}

export interface CompleteAnalysis {
  success: boolean;
  deviceId: string;
  dateRange: {
    startDate: string;
    endDate: string;
  };
  routeHistory: {
    success: boolean;
    data: RoutePoint[];
    totalPoints: number;
  };
  mileageReport: {
    success: boolean;
    data: MileageReport | null;
  };
  workingHours: {
    success: boolean;
    data: WorkingHoursReport | null;
  };
  heatMap: {
    success: boolean;
    data: HeatPoint[];
    totalPoints: number;
  };
}

const routeAnalysisService = {
  // Rota gemiini getir
  getRouteHistory: async (
    deviceId: string, 
    startDate: string, 
    endDate: string
  ): Promise<RoutePoint[]> => {
    try {
      console.log(` Rota gemii isteniyor - ${deviceId}: ${startDate}  ${endDate}`);
      
      const response = await apiService.get<{
        success: boolean;
        data: RoutePoint[];
        totalPoints: number;
      }>(`/route-analysis/route-history/${deviceId}?startDate=${startDate}&endDate=${endDate}`);
      
      console.log(` ${response.totalPoints} rota noktas alnd`);
      return response.data;
    } catch (error) {
      console.error(' Rota gemii hatas:', error);
      return [];
    }
  },

  // Kilometre raporunu getir  
  getMileageReport: async (
    deviceId: string,
    startDate: string,
    endDate: string
  ): Promise<MileageReport | null> => {
    try {
      console.log(` Kilometre raporu isteniyor - ${deviceId}`);
      
      const response = await apiService.get<MileageReport>(`/route-analysis/mileage-report/${deviceId}?startDate=${startDate}&endDate=${endDate}`);
      
      console.log(` Kilometre raporu alnd - ${response.totalDistance} km`);
      return response;
    } catch (error) {
      console.error(' Kilometre raporu hatas:', error);
      return null;
    }
  },

  // alma saati raporunu getir
  getWorkingHoursReport: async (
    deviceId: string,
    startDate: string, 
    endDate: string
  ): Promise<WorkingHoursReport | null> => {
    try {
      console.log(` alma saati raporu isteniyor - ${deviceId}`);
      
      const response = await apiService.get<WorkingHoursReport>(`/route-analysis/working-hours/${deviceId}?startDate=${startDate}&endDate=${endDate}`);
      
      console.log(` alma saati raporu alnd - ${response.totalWorkingHours} saat`);
      return response;
    } catch (error) {
      console.error(' alma saati raporu hatas:', error);
      return null;
    }
  },

  // Heat map verilerini getir
  getHeatMapData: async (
    deviceId: string,
    startDate: string,
    endDate: string
  ): Promise<HeatPoint[]> => {
    try {
      console.log(` Heat map verisi isteniyor - ${deviceId}`);
      
      const response = await apiService.get<{
        success: boolean;
        data: HeatPoint[];
        totalPoints: number;
      }>(`/route-analysis/heat-map/${deviceId}?startDate=${startDate}&endDate=${endDate}`);
      
      console.log(` ${response.totalPoints} heat point alnd`);
      return response.data;
    } catch (error) {
      console.error(' Heat map hatas:', error);
      return [];
    }
  },

  // Komple analiz - Tm verileri tek seferde getir
  getCompleteAnalysis: async (
    deviceId: string,
    startDate: string,
    endDate: string
  ): Promise<CompleteAnalysis | null> => {
    try {
      console.log(` Komple analiz isteniyor - ${deviceId}: ${startDate}  ${endDate}`);
      
      const response = await apiService.get<CompleteAnalysis>(`/route-analysis/complete-analysis/${deviceId}?startDate=${startDate}&endDate=${endDate}`);
      
      console.log(` Komple analiz alnd:`, {
        routePoints: response.routeHistory.totalPoints,
        mileageSuccess: response.mileageReport.success,
        heatPoints: response.heatMap.totalPoints
      });
      
      return response;
    } catch (error) {
      console.error(' Komple analiz hatas:', error);
      return null;
    }
  },

  // Test endpoint'i
  testConnection: async (deviceId: string): Promise<any> => {
    try {
      console.log(` Rota analizi test - ${deviceId}`);
      
      const response = await apiService.get<any>(`/route-analysis/test/${deviceId}`);
      
      console.log(' Test baarl:', response.message);
      return response;
    } catch (error) {
      console.error(' Test hatas:', error);
      throw error;
    }
  }
};

export default routeAnalysisService;