// services/employeeService.ts - Tam gncellenmi hali
import apiService from './apiService';

// Interface ismini IEmployee olarak deitirin
export interface IEmployee {
  personel_id: number;
  first_name: string;
  last_name: string;
  email: string;
  role: 1; // Sadece alanlar (rol = 1)
  avatar?: string;
  position?: string;
  department?: string;
  phone_number?: string;
  location?: string;
  hire_date?: string;
  birthday?: string;
  education?: string;
  skills?: string[];
  // Performance tipini ekliyoruz
  performance?: {
    current: number;
    history: number[];
    tasks: {
      completed: number;
      inProgress: number;
      overdue: number;
    };
    attendance: number;
    goals: {
      name: string;
      progress: number;
    }[];
  };
  projects?: {
    id: string;
    name: string;
    status: 'completed' | 'active' | 'planned';
    role: string;
  }[];
}

// Deerlendirme interface'i
export interface IEmployeeReview {
  id: number;
  puan: number;
  yorum: string;
  tarih: string;
  firma_adi: string;
  seri_no?: string;
  servis_tarihi?: string;
  servis_aciklama?: string;
}

// nce yeni bir interface ekleyin (dosyann IEmployeeReview'dan sonrasna)
export interface IEmployeeWork {
  id: number;
  baslik: string;
  aciklama?: string;
  tarih: string;
  durum: 'tamamlandi' | 'devam_ediyor' | 'beklemede';
  musteri_adi?: string;
  seri_no?: string;
}

// Sonra IEmployeeDetail'i dzeltin
export interface IEmployeeDetail {
  calisan: IEmployee;           // any yerine IEmployee
  degerlendirmeler: IEmployeeReview[];
  son_isler: IEmployeeWork[];   // any[] yerine IEmployeeWork[]
}

const employeeService = {
  // Tm alanlar getir
  getAllEmployees: async (): Promise<IEmployee[]> => {
    try {
      console.log(' alanlar getiriliyor...');
      const data = await apiService.get<IEmployee[]>('/calisanlar');
      console.log(' alanlar getirildi:', data);
      return data;
    } catch (error) {
      console.error(' alanlar getirilirken hata:', error);
      throw error;
    }
  },

  // alan deerlendirmelerini getir
  getEmployeeReviews: async (personelId: number): Promise<IEmployeeReview[]> => {
    try {
      console.log(' alan deerlendirmeleri getiriliyor:', personelId);
      const data = await apiService.get<IEmployeeReview[]>(`/calisan-degerlendirmeleri/${personelId}`);
      console.log(' Deerlendirmeler getirildi:', data);
      return data;
    } catch (error) {
      console.error(' Deerlendirmeler getirilirken hata:', error);
      return []; // Hata durumunda bo array dndr
    }
  },

  // alan detaylarn getir
  getEmployeeDetail: async (personelId: number): Promise<IEmployeeDetail> => {
    try {
      console.log(' alan detay getiriliyor:', personelId);
      const data = await apiService.get<IEmployeeDetail>(`/calisan-detay/${personelId}`);
      console.log(' alan detay getirildi:', data);
      return data;
    } catch (error) {
      console.error(' alan detay getirilirken hata:', error);
      throw error;
    }
  },

  // Yeni alan olutur
  createEmployee: async (employeeData: Omit<IEmployee, 'personel_id'>): Promise<IEmployee> => {
    try {
      console.log(' Yeni alan oluturuluyor:', employeeData);
      const data = await apiService.post<IEmployee>('/calisanlar', employeeData);
      console.log(' alan oluturuldu:', data);
      return data;
    } catch (error) {
      console.error(' alan oluturulurken hata:', error);
      throw error;
    }
  },

  // alan gncelle
  updateEmployee: async (personel_id: number, employeeData: Partial<IEmployee>): Promise<IEmployee> => {
    try {
      console.log(' alan gncelleniyor:', personel_id, employeeData);
      const data = await apiService.put<IEmployee>(`/calisanlar/${personel_id}`, employeeData);
      console.log(' alan gncellendi:', data);
      return data;
    } catch (error) {
      console.error(' alan gncellenirken hata:', error);
      throw error;
    }
  },

  // alan sil
  deleteEmployee: async (personel_id: number): Promise<void> => {
    try {
      console.log(' alan siliniyor:', personel_id);
      await apiService.delete(`/calisanlar/${personel_id}`);
      console.log(' alan silindi:', personel_id);
    } catch (error) {
      console.error(' alan silinirken hata:', error);
      throw error;
    }
  }
};

export default employeeService;